import uniIcon from '@/components/uni-icon/uni-icon.vue'

export default {
	name: 'BaseInfo',
	components: {
		uniIcon
	},
	data() {
		return {
			task: {}
		}
	},
	methods: {
		isFrequencySel (frequency) {
			if(this.task.frequency === frequency) return 'sel ' + this.task.color;
		}
	},
	mounted () {
		 this.task = this.$store.state.task;
	}
}
